.header{
    display: flex;
justify-content: last baseline;
padding: 1rem;
position: fixed;
width: 100%;
height: 90px;
z-index: 10;
}
.nav-menu li{
    padding: 0 1rem;
}
.nav-menu{
    display: flex;
}
.nav-menu li a {
    font-size: 1.2rem;
    font-weight: 500;
}
.harmburger{
    display: none;
}
@media screen and (max-width:1040px) {
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, 0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        

    }
    
    .nav-menu.active{
        left: 0;
    }
    .nav-menu li{
        padding: 1rem 0;
    }
    .nav-menu li a{
        font-size: 2rem;
    }
    .harmburger{
        display: initial;
    }
    
    
    
}