@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,300;1,300&family=Outfit:wght@100;200;300;400;500;600;700;800;900&family=Work+Sans:wght@100&display=swap');
*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;

font-family: 'Outfit', sans-serif;

}
body{
  background: black;
}
h1,h4,p,a{
  color: #fff;
  text-decoration: none;
}
ul{
  list-style-type: none;
}
.btn{
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background: rgb(248, 217, 15);
  color: #fff;
  border: 1px solid #fff;
  font-weight: 600;
  cursor: pointer;
}
.btn-light{
  background: transparent;
  color: #fff;
}
.btn:hover{
  background: rgb(255, 255, 255,0.2);
  color: #fff;
  transition: 0.3s;
}